.button {
  display: flex;
  justify-content: center;
  align-items: center;
  
  font-family: inherit;
  padding: 13px 20px;
  height: 44px;

  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;

  border-radius: 5px;
  border: none;

  cursor: pointer;

  color: gray;
  background-color: transparent;
  border: 1px solid var(--text-films-second);
  
 &:hover {
    background-color: var(--text-films-second);
    color: #fff;
 }
}

.tui-page-btn {
  color: #ff6b01;
}


// Pagination btn style

 .tui-pagination * {
     -moz-user-select: none;
     -webkit-user-select: none;
     -ms-user-select: none;
     user-select: none;
 }
 
 .tui-ico-ellipsis, .tui-ico-first, .tui-ico-last, .tui-ico-next, .tui-ico-prev {
     overflow: hidden;
 }
 
 .tui-pagination {
     height: 28px;
     margin: 14px 0 12px;
     line-height: normal;
     text-align: center;
     font-size: 0;
 }
 
 .tui-pagination .tui-page-btn {
     display: inline-block;
     position: relative;
     width: 28px;
     padding: 8px 0 6px;
     margin-left: 0;
     color: #333;
     font-size: 12px;
     font-weight: normal;
     line-height: 1;
     text-decoration: none;
     vertical-align: middle;
     border: 3px solid rgb(255, 107, 8);
     border-width: 2px 0;
     border-radius: 8px;
 }

 
 .tui-pagination .tui-page-btn:hover {
     background-color: rgb(255, 107, 8);
 }
 
 .tui-pagination .tui-is-selected, .tui-pagination strong {
     color: #fff;
     background: rgb(255, 107, 8);
     border-color: rgb(255, 107, 8);
     cursor: default;
 }
 
 .tui-pagination .tui-is-selected:hover {
     background-color: rgb(255, 107, 8);
 }
 
 .tui-pagination .tui-first-child.tui-is-selected, .tui-pagination strong.tui-first-child {
     margin-left: 0
 }
 
 .tui-pagination .tui-first-child, .tui-pagination .tui-prev-is-ellip {
     border-left: 2px solid rgb(255, 107, 8)
 }
 
 .tui-pagination .tui-last-child, .tui-pagination .tui-next-is-ellip {
     border-right: 2px solid rgb(255, 107, 8)
 }
 
 .tui-pagination .tui-first-child.tui-is-selected {
     border-left: 2px solid rgb(255, 107, 8)
 }
 
 .tui-pagination .tui-last-child.tui-is-selected {
     border-right: 2px solid rgb(255, 107, 8)
 }
 
 .tui-pagination .tui-first, .tui-pagination .tui-prev, .tui-pagination .tui-next, .tui-pagination .tui-last, .tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
     overflow: hidden;
     height: 26px;
     padding: 0;
     font-size: 0;
     line-height: 26px;
     white-space: nowrap;
     font-weight: normal;
     border: 3px solid #ff6b08;
     border-radius: 8px;
 }
 
 .tui-pagination .tui-prev-is-ellip {
     border-right: 2px;
 }
 
//  .tui-pagination .tui-next-is-ellip {
//     //  border-left: 2px;
//  }
 
 .tui-pagination .tui-first+.tui-prev, .tui-pagination .tui-next+.tui-last {
     border-left: 2px;
 }
 
 .tui-pagination .tui-ico-first, .tui-pagination .tui-ico-prev, .tui-pagination .tui-ico-next, .tui-pagination .tui-ico-last, .tui-pagination .tui-ico-ellip {
     background: url(data:image/png;base64,) no-repeat 0 0;
     display: inline-block;
     vertical-align: middle;
     font-size: 0px;
 }
 
 .tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
     padding: 0;
     line-height: 1;
 }
 
 .tui-pagination .tui-prev {
     margin-right: 10px;
 }
 
 .tui-pagination .tui-next {
     margin-left: 10px;
 }
 
 .tui-pagination .tui-ico-first {
     width: 11px;
     height: 10px;
     background-position: 0px 0px
 }
 
 .tui-pagination .tui-ico-prev {
     width: 6px;
     height: 10px;
     background-position: -26px 0px
 }
 
 .tui-pagination .tui-ico-next {
     width: 6px;
     height: 10px;
     background-position: -13px -24px
 }
 
 .tui-pagination .tui-ico-last {
     width: 11px;
     height: 10px;
     background-position: 0px -12px
 }
 
 .tui-pagination .tui-ico-ellip {
     width: 10px;
     height: 2px;
     margin-top: 16px;
     vertical-align: top;
     background-position: 0px -24px
 }
 
 .tui-pagination .tui-is-disabled:hover {
     background: rgb(255, 107, 8);
     cursor: default
 }
 
 .tui-pagination .tui-is-disabled .tui-ico-prev {
     background-position: -26px -12px
 }
 
 .tui-pagination .tui-is-disabled .tui-ico-first {
     background-position: -13px 0px
 }
 
 .tui-pagination .tui-is-disabled .tui-ico-next {
     background-position: -21px -24px
 }
 
 .tui-pagination .tui-is-disabled .tui-ico-last {
     background-position: -13px -12px
 }
 
 .tui-page-inputs {
     display: inline-block;
     vertical-align: top;
 }
 
 .tui-page-inputs .tui-page-input {
     display: inline-block;
     width: 10px;
     height: 12px;
     padding: 8px 0 6px;
     font-size: 12px;
     text-align: center;
     color: #333;
     border: 0;
     outline: none;
 }
 
 .tui-page-inputs .tui-page-current {
     color: rgb(255, 107, 8)
 }
 
 .tui-page-separator {
     display: inline-block;
     font-size: 12px;
     height: 12px;
     padding: 8px 0 6px;
     vertical-align: top
 }
 
 