.theme-switch {
  z-index: 0;
  align-items: center;
  display: flex;
}

.theme-switch__icon {
  height: 20px;
  width: 20px;
  margin-left: 8px;
  margin-right: 8px;
}

.theme-switch__toggle {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: #0000;
  -webkit-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #e3d1d1;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  width: 12px;
  height: 12px;
  z-index: 2;
  pointer-events: none;
  background-color: #ab3030;
  border-radius: 50%;
  transition: transform .25s;
  position: relative;
  transform: translateX(3px);
  box-shadow: 0 1px 1px #00000040;
}

.theme-switch__toggle:checked + .theme-switch__track {
  background-color: #000;
}

.theme-switch__toggle:checked ~ .theme-switch__marker {
  transform: translateX(35px);
}

.theme-switch__control {
  width: 50px;
  height: 15px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
